import request from '@/utils/request'

// 
export function listArticle(articleQuery) {
    return request({
        url: '/blog/car/listPage',
        method: 'get',
        params: articleQuery
    })
}

// 
export function listPark(parkMsg) {
    return request({
        url: '/blog/park/listMemberPage',
        method: 'get',
        params: parkMsg
    })
}

// 
export function getArticleDetail(carId) {
    return request({
        url: '/blog/car/' + carId,
        method: 'get',
    })
}

// 
export function addArticle(car) {
    return request({
        url: '/blog/car',
        method: 'post',
        data: car   // json
    })
}

// 
export function updateCarN(parkMsg) {
    return request({
        url: '/blog/park',
        method: 'put',
        data: parkMsg   // json
    })
}

// 
export function deleteArticle(carIds) {
    return request({
        url: '/blog/car/' + carIds,
        method: 'delete',
    })
}