import request from '@/utils/request'

// 
export function getUser(username) {
    return request({
        url: '/blog/user/' + username,
        method: 'get',
    })
}

//
export function getCardMsg(username) {
    return request({
        url: '/blog/user/card/' + username,
        method: 'get',
    })
}

//
export function getCostHour() {
    return request({
        url: '/blog/user/cost/',
        method: 'get',
    })
}

// 
export function updateUser(user) {
    return request({
        url: '/blog/user',
        method: 'put',
        data: user   // json
    })
}

// 
export function changePwd(info) {
    return request({
        url: '/blog/user',
        method: 'post',
        data: info   // json
    })
}