import request from '@/utils/request'

// 
export function login(loginData) {
    return request({
        url: '/login',
        method: 'post',
        data: loginData
    })
}

// 
export function loginadmin(loginData) {
    return request({
        url: '/login/admins',
        method: 'post',
        data: loginData
    })
}

// 
export function getLoggedInUserInfo() {
    return request({
        url: '/getLoggedInUserInfo',
        method: 'get',
    })
}

// 
export function logout() {
    return request({
        url: '/logout',
        method: 'get',
    })
}