import request from '@/utils/request'

var urlStr = "/park/admin/userMsg"

// 
export function listUser(adminQuery) {
    return request({
        url: urlStr + '/listUser',
        method: 'get',
        params: adminQuery
    })
}

// 
export function addUser(userMsg) {
    return request({
        url: urlStr + '/addUser',
        method: 'post',
        data: userMsg   // json
    })
}

// 
export function addCard(userMsg) {
    return request({
        url: urlStr + '/addCard',
        method: 'post',
        data: userMsg   // json
    })
}

// 
export function updateCard(userMsg) {
    return request({
        url: urlStr + '/updateCard',
        method: 'put',
        data: userMsg,      // json
    })
}

// 
export function updateUser(userMsg) {
    return request({
        url: urlStr + '/update',
        method: 'put',
        data: userMsg,      // json
    })
}

// 
export function deleteUser(userIds) {
    return request({
        url: urlStr + '/delete/' + userIds,
        method: 'delete',
    })
}