import request from '@/utils/request'

// 
export function listArticle(articleQuery) {
    return request({
        url: '/blog/car/listPage',
        method: 'get',
        params: articleQuery
    })
}

// 
export function listRecord(recordMsg) {
    return request({
        url: '/blog/record/listMemberPage',
        method: 'get',
        params: recordMsg
    })
}