<template>
  <div>
    <v-card elevation="5" class="mb-2">
      <v-card-title>
        <v-icon class="mx-3">$edit</v-icon>
        {{ action }}
      </v-card-title>
      <v-card-text>
        <v-form v-model="valid" ref="carForm">
          <v-container>
            <v-row dense>
              <v-col lg="6" cols="12">
                <v-text-field
                  v-model="carCopy.carNumber"
                  label=""
                  outlined
                  :rules="carRules"
                  dense
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row dense>
              <v-col lg="6" cols="12">
                <v-text-field
                  v-model="carCopy.carName"
                  label=""
                  outlined
                  dense
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row dense>
              <v-col lg="6" cols="12">
                <v-file-input
                  v-model="Photo"
                  :rules="PhotoRules"
                  accept="image/png, image/jpeg, image/bmp"
                  prepend-icon="mdi-camera"
                  show-size
                  dense
                  outlined
                  label=""
                ></v-file-input>
              </v-col>
            </v-row>

            <v-row dense>
              <v-col lg="6" cols="12">
                <v-text-field
                  v-model="carCopy.note"
                  label="()"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
            </v-row>

            <v-row dense class="mt-4 mb-2">
              <v-col lg="6" cols="12" class="text-center">
                <v-btn
                  color="primary"
                  dark
                  small
                  rounded
                  class="mr-4"
                  @click="saveCar()"
                  ></v-btn
                >
                <v-btn color="orange" dark small rounded @click="resetForm()"
                  ></v-btn
                >
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import {
  addArticle,
  getArticleDetail,
  updateArticle,
} from "@/api/blog/article";
import { showSuccessMsg } from "@/utils/msg";

export default {
  name: "CarEditor",
  props: ["carId"],
  data: () => ({
    article: {},
    carCopy: {},
    Photo: null,
    valid: true,
    validContent: true,
    carRules: [
      (v) => !!v || "",
      (v) =>
        (v &&
          /^(([][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]))$/.test(
            v
          )) ||
        "",
    ],
    PhotoRules: [
      (v) => !!v || "",
      (v) =>
        (v && /\.(bmp|jpg|gif|jpeg|png)$/i.test(v.name)) ||
        "jpg,jpeg,bmp,png",
      (v) => !v || v.size < 1024 * 1024 * 1 || "1MB!",
    ],
    page: 1,
    pageSize: 10,
    totalPages: 0,
  }),
  created() {
    if (this.carId > 0) {
      // 
      this.getArticleDetail();
    }
  },
  computed: {
    action() {
      return this.carId > 0 ? "" : "";
    },
  },
  methods: {
    getArticleDetail() {
      getArticleDetail(this.carId).then((result) => {
        console.log(result.data.note);
        this.article = result.data;
        console.log(this.article);
        // 
        this.carCopy = Object.assign({}, this.article);
      });
    },
    imgAdd() {
      //
      let formdata = new FormData();
      formdata.append("file", this.Photo);
      formdata.append(
        "car",
        new Blob([JSON.stringify(this.carCopy)], { type: "application/json" })
      );
      console.log(this.carCopy);
      return formdata;
    },
    updateAdd() {
      if (this.carId > 0) {
        //
        updateArticle(this.imgAdd()).then((result) => {
          //
          showSuccessMsg(result.msg);
          //
          this.article = Object.assign({}, this.carCopy);
        });
      } else {
        //
        addArticle(this.imgAdd()).then((result) => {
          showSuccessMsg(result.msg);
          this.resetForm();
        });
      }
    },
    saveCar() {
      //
      //
      if (this.$refs.carForm.validate()) {
        this.updateAdd();
      }
    },
    resetForm() {
      // 
      this.carCopy = Object.assign({}, this.article);
      this.validContent = true;
      this.$refs.carForm.resetValidation();
    },
    // @imgAdd event
    // $imgAdd(pos, $file) {
    //   let formdata = new FormData();
    //   formdata.append("uploadedFiles", $file);
    //   fileUpload(formdata).then((result) => {
    //     console.log(result);
    //     if (result.data && result.data.length == 1) {
    //       this.$refs.md.$img2Url(pos, result.data[0]);
    //     }
    //   });
    // },
    $imgDel() {},
  },
};
</script>


<style lang="scss" scoped>
// mavon-editorz
.v-note-wrapper {
  z-index: inherit;
}
</style>
